import { buy, sell } from "../state/Main";
import * as React from "react";
import { connect, ConnectedProps } from "react-redux";
import * as _ from "lodash";
import {
	Commodity,
	Transaction,
	State,
	Rarity,
	ResourceType
} from "~ts/types/State";
import { PayloadAction } from "@reduxjs/toolkit";
import { __ } from "~ts/i18n/convert";
import {
	TableHead,
	TableCell,
	TableRow,
	Table,
	InputLabel,
	TextField,
	Button,
	FormGroup,
	Grid,
	List,
	ListItem,
	ListItemText
} from "@material-ui/core";

function rarityValues(a: Rarity): string {
	switch (a) {
		case Rarity.Fuel:
			return "1";
		case Rarity.Common:
			"0-150";
		default:
			return "500-1000";
	}
}

interface TradeProps extends PropsFromRedux {
	// Provided by parent
	isSelling: boolean;
}

interface TradeState {
	selection?: ResourceType;
	price?: number;
	quantity?: number;
}
const mapStateToProps = (state: State) => {
	return {
		completedTransactions: state.current.completedTransactions || [],
		currentWeight: state.player.currentWeight,
		maxWeight: state.player.maxWeight,
		playerCash: state.player.cash,
		playerGoods: state.player.goods,
		townGoods: state.locations[state.player.location].goods,
		msg: __(state.current.message)
	};
};

const mapDispatch = { buy, sell };

class UnconnectedTradeMenu extends React.Component<TradeProps, TradeState> {
	state: TradeState = {
		price: null,
		quantity: null,
		selection: null
	};

	selectGood(id: ResourceType) {
		this.setState({
			selection: id
		});
	}

	render() {
		let { selection, price, quantity } = this.state;
		let {
			townGoods,
			completedTransactions,
			isSelling,
			playerGoods,
			currentWeight,
			maxWeight,
			playerCash,
			msg,
			buy,
			sell
		} = this.props;
		let playerVersion = _.find(playerGoods, good => good.id === selection);
		let townVersion = townGoods[selection];
		let goodQuantity = isSelling
			? (playerVersion && playerVersion.quantity) || 0
			: (townVersion && townVersion.quantity) || 0;
		let goodWeight =
			(quantity || 0) * ((townVersion && townVersion.weight) || 0);
		let goodTotalPrice = (quantity || 0) * (price || 0);
		let goodList = _.map(townGoods, (good, id: ResourceType) => {
			let tv = townGoods[id];
			let pv = _.find(playerGoods, good => good.id === id);
			let disabled =
				_.some(completedTransactions, t => t === id) ||
				(isSelling
					? pv == null || pv.quantity <= 0
					: tv == null || tv.quantity <= 0);
			let checked = !disabled && id === selection;
			return (
				<TableRow
					className={disabled ? ".Mui-disabled" : ""}
					onClick={() => this.selectGood(id)}
					id={id}
					key={id}
					selected={checked}
				>
					<TableCell>{good.name}</TableCell>
					<TableCell>{good.weight}</TableCell>
					<TableCell>
						{Rarity[good.rarity] + " (" + rarityValues(good.rarity) + ")"}
					</TableCell>
					<TableCell>
						{isSelling ? (pv && pv.quantity) || 0 : tv.quantity}
					</TableCell>
				</TableRow>
			);
		});
		let purchaseDisabled =
			_.some(completedTransactions, t => t === selection) ||
			goodWeight + currentWeight > maxWeight ||
			(goodTotalPrice > playerCash && !isSelling) ||
			goodQuantity === 0 ||
			quantity == null ||
			quantity == NaN ||
			(quantity as unknown) == "";
		let purchaseFunc = isSelling ? sell : buy;
		let buttonName = isSelling ? "SELL_BUTTON" : "BUY_BUTTON";
		let numProps = { min: "1", step: "1" };
		return (
			<Grid className="container--trade">
				<FormGroup>
					<Table className="input-table">
						<TableHead className="input-row">
							<TableCell>Name</TableCell>
							<TableCell>Weight</TableCell>
							<TableCell>Rarity (Price Range)</TableCell>
							<TableCell>Available</TableCell>
						</TableHead>
						{goodList}
					</Table>
					<div>
						<InputLabel htmlFor="price">Price:</InputLabel>
						<TextField
							id="price"
							type="number"
							inputProps={numProps}
							value={price || ""}
							onChange={ev =>
								this.setState({ price: parseInt(ev.target.value) || 0 })
							}
						/>
						<InputLabel htmlFor="quantity">Amount:</InputLabel>
						<TextField
							id="quantity"
							type="number"
							inputProps={numProps}
							value={quantity || ""}
							onChange={ev =>
								this.setState({ quantity: parseInt(ev.target.value) || 0 })
							}
						/>
					</div>
					<div className="warnings"></div>
					<Button
						disabled={purchaseDisabled}
						onClick={() =>
							purchaseFunc({
								goodId: selection,
								price: price,
								quantity: quantity
							})
						}
					>
						{__(buttonName)}
					</Button>
				</FormGroup>
				<List>
					<ListItem>
						<ListItemText>{msg}</ListItemText>
					</ListItem>
				</List>
			</Grid>
		);
	}
}

const connector = connect(mapStateToProps, mapDispatch);

type PropsFromRedux = ConnectedProps<typeof connector>;

export const TradeMenu = connector(UnconnectedTradeMenu);
