import {
	State,
	Message,
	Commodity,
	Rarity,
	ResourceType
} from "../types/State";
import * as React from "react";
import _ from "lodash";
import { connect } from "react-redux";
import { Table, TableCell, TableHead, TableRow } from "@material-ui/core";

const mapStateToProps = (state: State) => {
	return {
		playerGoods: state.player.goods
	};
};

const UnconnectedInventoryMenu = ({
	playerGoods
}: {
	playerGoods: Record<ResourceType, Commodity>;
}) => {
	return (
		<Table className="container--inventory">
			<TableHead className="header">
				<TableCell>Name</TableCell>
				<TableCell>Quantity</TableCell>
				<TableCell>Rarity</TableCell>
			</TableHead>
			{_.map(playerGoods, good => (
				<TableRow key={good.id} className="inventory-item">
					<TableCell>{good.name}</TableCell>
					<TableCell>{good.quantity}</TableCell>
					<TableCell>{Rarity[good.rarity]}</TableCell>
				</TableRow>
			))}
		</Table>
	);
};

export const InventoryMenu = connect(mapStateToProps)(UnconnectedInventoryMenu);
